import React from "react";
import ReactDOM from "react-dom";
import CalendarGrid from "./components/CalendarGrid";
import data from "./data/calendar-grid-data";
import zipObject from "lodash/zipObject";
import moment from "moment";
import "../css/calendar-grid.scss";

const RAW_DATE_FORMAT = "YYYYMMDD hh:mm:ss";
const DATE_FORMAT = "MMM DD";

const cleanData = [];
cleanData.push(
    data.columns.map(c => {
        return {
            id: c.name,
            value: c.title
        };
    })
);
const columnNames = data.columns.map(c => c.name);
data.rows.forEach(row =>
    cleanData.push(
        columnNames.map((colName, idx) => {
            return {
                id: colName,
                value: row[idx]
            };
        })
    )
);

const cellRender = data => {
    const values = data.value 
        ? data.value.split(/\n/)
        : [];
    let content;
    if (data.id === "magical_release_date") {
        const date = moment(values[0], RAW_DATE_FORMAT);
        if (date.isValid()) {
            content = (
                <React.Fragment>
                <span className="cell-title">{date.format(DATE_FORMAT)}</span><br/>
                <span className="cell-subtitle">{date.year()}</span><br/>
                {values[1] && (<span>{values[1].replace("not_date:", "")}</span>)}
                </React.Fragment>
            );
        }
    } 
        
    content = content || (
        <React.Fragment>
        { values.slice(0, 3).map((v, idx) => (
            <React.Fragment key={idx}>
                <span className="cg-event" dangerouslySetInnerHTML={{ __html: v }} /><br/>
            </React.Fragment>)
          )
        }
        { values.slice(3, values.length).length > 0 
            && (<div className="event-detail">
                {
                    values.slice(3, values.length).map((v, idx) => (
                        <React.Fragment key={idx}>
                            <span className="cg-event" dangerouslySetInnerHTML={{ __html: v }} /><br/>
                        </React.Fragment>)
                      )
                }
              </div>)
        }
        </React.Fragment>
    );

    return content;
};

const preprocessData = (rows, config) => {
    const requiredDimensions = [ 
        "rs_release_calendar_summary.release_date", 
        "rs_release_calendar_summary.distributor_name"
    ];
    const {
        dimensions
    } = config.query_fields;
    const dimensionByName = dimensions.reduce((hash, dimension) => {
        return { 
            ...hash, 
            [ dimension.name ]: {
                ...dimension,
                short_name: dimension.name.split(".").pop()
            }
        };
    }, {});

    const canRender = requiredDimensions.every(name => dimensionByName[name]);
 
    if (canRender) {
        const distByName = {};

        //organize data by release_date and distributor_name  
        const distByRelDate = rows.reduce((hash, row) => {
            const relDate = row["rs_release_calendar_summary.release_date"].value;
            const distName = row["rs_release_calendar_summary.distributor_name"].value;
            hash[relDate] = hash[relDate] || {};

            if (distName) {
                distByName[distName] = {
                    name: distName,
                    value: distName
                };

                hash[relDate][distName] = hash[relDate][distName] || [];
                hash[relDate][distName].push(row);
            }

            return hash;
        }, {});

        
        const columns = [{ name: "release_date", value: "Release Date" }].concat(Object.values(distByName));
        console.log("Dist By Rel Date", distByRelDate, distByName, columns);
    }
};

looker.plugins.visualizations.add({
    create: function(element, config){
        console.log("CONFIG", config);
        ReactDOM.render(
            <CalendarGrid 
                data={cleanData} 
                cellRender={cellRender}
            />, 
            element
        );
    },
    updateAsync: function(data, element, config, queryResponse, details, doneRendering){
        preprocessData(data, config);
        console.log("UPDATE ASYNC IS CALLED", data,element,config, queryResponse, details, doneRendering);
        doneRendering();
    }
});
